<template>
  <div>
    <h3>
      <p>역할군</p>
    </h3>
    <div class="btn-group" role="group" aria-label="Basic checkbox toggle button group">
      <input type="checkbox" class="btn-check" id="btncheck1" v-model="d">
      <label class="btn btn-outline-success" for="btncheck1">타격대</label>

      <input type="checkbox" class="btn-check" id="btncheck2" v-model="i">
      <label class="btn btn-outline-success" for="btncheck2">척후대</label>

      <input type="checkbox" class="btn-check" id="btncheck3" v-model="s">
      <label class="btn btn-outline-success" for="btncheck3">감시자</label>

      <input type="checkbox" class="btn-check" id="btncheck4" v-model="c">
      <label class="btn btn-outline-success" for="btncheck4">전략가</label>
    </div>

    <div class="my-2">
      <button type="button" class="btn btn-primary" @click="check()">
        확인
      </button>
    </div>

    <div v-if="agentInfo.name">
      <AgentCard
        :agentName="agentInfo.name"
        :imageSrc="agentInfo.imageSrc"
        :englishName="agentInfo.englishName"
        :position="agentInfo.position"
      />
    </div>

  </div>
</template>

<script>
import AgentCard from '../components/AgentCard.vue'

export default {
  name: 'ValorantRandomPick',
  data() {
    return {
      d: true,
      i: true,
      s: true,
      c: true,

      duelist: [],
      initiator: [],
      sentinel: [],
      controller: [],

      list: [],

      agentObject: {},
      agentInfo: {},
    }
  },
  methods: {
    check() {
      this.list = []
      if (this.d) this.list.push("duelist")
      if (this.i) this.list.push("initiator")
      if (this.s) this.list.push("sentinel")
      if (this.c) this.list.push("controller")

      if (this.list.length == 0) alert("역할군을 선택하세요.")
      else {
        const positionName = this.list[this.getRandomIndex(this.list)]
        this.selectAgent(positionName)
      }
    },
    selectAgent(positionName) {
      const agentList = this.agentObject[positionName]
      const agentIndex = this.getRandomIndex(agentList)
      this.agentInfo = agentList[agentIndex]
    },
    getRandomIndex(list) {
      const result = Math.floor(Math.random() * list.length)
      return result
    },
  },
  mounted() {
    this.agentObject = {
      "duelist": [ // 타격대
        { name: "네온", englishName: "NEON", imageSrc: require("../images/duelist/NEON.jpg"), position: "타격대" },
        { name: "레이나", englishName: "REYNA", imageSrc: require("../images/duelist/REYNA.jpg"), position: "타격대" },
        { name: "레이즈", englishName: "RAZE", imageSrc: require("../images/duelist/RAZE.jpg"), position: "타격대" },
        { name: "아이소", englishName: "ISO", imageSrc: require("../images/duelist/ISO.jpg"), position: "타격대" },
        { name: "요루", englishName: "YORU", imageSrc: require("../images/duelist/YORU.jpg"), position: "타격대" },
        { name: "제트", englishName: "JETT", imageSrc: require("../images/duelist/JETT.jpg"), position: "타격대" },
        { name: "피닉스", englishName: "PHOENIX", imageSrc: require("../images/duelist/PHOENIX.jpg"), position: "타격대" },
      ],
      "initiator": [ // 척후대
        { name: "게코", englishName: "GEKKO", imageSrc: require("../images/initiator/GEKKO.jpg"), position: "척후대" },
        { name: "브리치", englishName: "BREACH", imageSrc: require("../images/initiator/BREACH.jpg"), position: "척후대" },
        { name: "소바", englishName: "SOVA", imageSrc: require("../images/initiator/SOVA.jpg"), position: "척후대" },
        { name: "스카이", englishName: "SKYE", imageSrc: require("../images/initiator/SKYE.jpg"), position: "척후대" },
        { name: "케이/오", englishName: "FADE", imageSrc: require("../images/initiator/FADE.jpg"), position: "척후대" },
      ],
      "sentinel": [ // 감시자
        { name: "데드록", englishName: "DEADLOCK", imageSrc: require("../images/sentinel/DEADLOCK.jpg"), position: "감시자" },
        { name: "사이퍼", englishName: "CYPHER", imageSrc: require("../images/sentinel/CYPHER.jpg"), position: "감시자" },
        { name: "세이지", englishName: "SAGE", imageSrc: require("../images/sentinel/SAGE.jpg"), position: "감시자" },
