        { name: "킬조이", englishName: "KILLJOY", imageSrc: require("../images/sentinel/KILLJOY.jpg"), position: "감시자" },
      ],
      "controller": [ // 전략가
        { name: "바이퍼", englishName: "VIPER", imageSrc: require("../images/controller/VIPER.jpg"), position: "전략가" },
        { name: "브림스톤", englishName: "BRIMSTONE", imageSrc: require("../images/controller/BRIMSTONE.jpg"), position: "전략가" },
        { name: "아스트라", englishName: "ASTRA", imageSrc: require("../images/controller/ASTRA.jpg"), position: "전략가" },
        { name: "오멘", englishName: "OMEN", imageSrc: require("../images/controller/OMEN.jpg"), position: "전략가" },
        { name: "클로브", englishName: "CLOVE", imageSrc: require("../images/controller/CLOVE.jpg"), position: "전략가" },
        { name: "하버", englishName: "HARBOR", imageSrc: require("../images/controller/HARBOR.jpg"), position: "전략가" },
      ]
    }

    // console.log(this.agentObject)
  },
  components: {
    AgentCard,
  }
}
</script>
